:root {
  --cube-size: 20px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: grid;
  font-family: sans-serif;
  background-color: #ccc;
  overflow: hidden;
  position: fixed;
}

/* New CSS */

.container {
  width: 600px;
  height: 600px;
  position: relative;
  perspective: 1000px;
  user-select: none;
  background-color: #222;
  overflow: hidden;
  transform-origin: 0 0;
  transition: background-color 0.2s;
}

.world {
  transform-style: preserve-3d;
  position: absolute;
}

.cube {
  position: absolute;
  transform-style: preserve-3d;
}

.cube .face {
  margin: 0;
  width: calc(var(--cube-size)*2);
  height: calc(var(--cube-size)*2);
  display: block;
  position: absolute;
  border: 1px solid #000;
}

.cube .front {
  transform: rotateY(0deg) translateZ(var(--cube-size));
}

.cube .back {
  transform: rotateX(180deg) translateZ(var(--cube-size));
}

.cube .right {
  transform: rotateY(90deg) translateZ(var(--cube-size));
}

.cube .left {
  transform: rotateY(-90deg) translateZ(var(--cube-size));
}

.cube .top {
  transform: rotateX(90deg) translateZ(var(--cube-size));
}

.cube .bottom {
  transform: rotateX(-90deg) translateZ(var(--cube-size));
}

.cube.p0 .face {
  background-color: #fff;
  opacity: 0.2;
}

.cube.p0:hover .face {
  opacity: 0.5;
}

.cube.p1 .face {
  background-color: #f00;
}

.cube.p2 .face {
  background-color: #0f0;
}

.cube.p3 .face {
  background-color: #00f;
}

.cube.p4 .face {
  background-color: #f0f;
}

.cube.p5 .face {
  background-color: #ff0;
}

.cube.p6 .face {
  background-color: #0ff;
}

.next {
  padding: 0.5rem 0.9rem;
  border-radius: 15%;
}

.menu p {
  margin: 1rem;
}

.next.p1 {
  background-color: #f00;
  color: #fff;
}

.next.p2 {
  background-color: #0f0;
}

.next.p3 {
  background-color: #00f;
  color: #fff;
}

.next.p4 {
  background-color: #f0f;
  color: #fff;
}

.next.p5 {
  background-color: #ff0;
}

.next.p6 {
  background-color: #0ff;
}

.winner {
  font-size: 1.5rem;
  line-height: 1.8;
}

.menu {
  position: fixed;
  background-color: #fff;
  padding: 2rem;
  text-align: center;
}

.start-to-effect {
  padding: 0.5rem;
  color: #800;
}

.start-to-effect.invisible {
  display: none;
}

.menu select, .menu button {
  margin-top: 1rem;
  padding: .75rem 1.5rem;
  border: none;
  border-radius: .5rem;
  background-color: #777;
  color: white;
  appearance: none;
  width: 100%;
  font-size: 1.2rem;
  text-align: center;
  text-align-last: center;
}

@media (orientation: landscape) {
  .menu {
    top: 0;
    bottom: 0;
    right: 0;
    width: 35%;
  }
}

@media (orientation: portrait) {
  .menu {
    bottom: 0;
    right: 0;
    left: 0;
    font-size: 3rem;
  }

  .menu button, .menu select {
    padding: 1.5rem 3rem;
    font-size: 3rem;
  }

  .winner {
    font-size: 3rem;
    line-height: 1.8;
  }
}